import React from 'react';
import { Popover, Button, Icon } from 'antd';

class ScheduleItem extends React.Component {
  renderContent = () => {
    const {
      data: { title, time, room, teacher },
    } = this.props;
    return (
      <div className="popover-schedule-item">
        <div className="schedule-item-row">
          <div className="schedule-item-title">{title}</div>
        </div>
        <div className="schedule-item-row">
          <Icon type="clock-circle" />
          <div>{time}</div>
        </div>
        <div className="schedule-item-row">
          <Icon type="environment" />
          <div>{room}</div>
        </div>
        <div className="schedule-item-row">
          <Icon type="solution" />
          <div>{teacher}</div>
        </div>
      </div>
    );
  };

  render() {
    return (
      <div className="event-wrapper">
        <Popover placement="right" content={this.renderContent()} trigger="click">
          <Button className="event" type="primary" size="small">
            {this.props.data.title}
          </Button>
        </Popover>
      </div>
    );
  }
}

export default ScheduleItem;



